@media (min-width: 768px) {
    .mobile-header {
        display: none;

        .arrow-back {
            cursor: pointer;
        }
    }

    .event-details {
        margin: 0 auto;
        padding-top: 32px;
        width: 900px;

        .event-details-thumbnail {
            display: inline-block;
            max-width: 400px;
            vertical-align: top;
            width: 40%;
        }

        .event-details-wrapper {
            display: inline-block;
            width: 60%;
            padding: 16px;

            .event-details-title {
                font-size: 1.5rem;
                font-weight: 700;
                padding: 0 16px;
            }

            .event-details-desc {
                font-size: 1rem;
                font-weight: 400;
                padding: 0 16px;

                .title {
                    font-size: 1rem;
                    color: #000;
                }

                .event-details-link {
                    //text-decoration: underline;
                    padding: 0;
                    cursor: pointer;
                }
            }

            .event-details-link {
                //text-decoration: underline;
                padding: 0 16px;
                cursor: pointer;
            }
        }

        .event-details-cards {
            margin: 0 16px 10px;
            background-color: white;
            border-radius: 25px;
            padding: 24px 16px;

            .title {
                font-size: 1.3rem;
                font-weight: 400;
            }

            .item {
                padding-top: 16px;
                vertical-align: top;

                .icon {
                    display: inline-block;
                    padding-top: 5px;
                    vertical-align: top;
                    width: 30px;
                }

                .info {
                    color: #2c2c2c;
                    display: inline-block;
                    font-size: 0.875rem;
                    font-weight: 400;

                    &.warning {
                        color: red;
                    }
                }

                .warning {
                    color: red;
                    display: inline-block;
                    font-size: 0.875rem;
                    font-weight: 400;
                }


            }
        }

        .event-media-list{
            margin-bottom: 50px;
            padding: 32px 0 16px;

            .title{
                padding-left: 0;
                margin-bottom: 8px;
            }

            .media-list{
                height: 270px;
                overflow-x: scroll;
                padding: 10px 0;
                position: relative;
                white-space: nowrap;
                width:100%;

                .media-list-item{
                    background-color: white;
                    border-radius: 12px;
                    cursor: pointer;
                    display: inline-flex;
                    flex-direction: column;
                    height: 240px;
                    margin-right: 8px;
                    overflow: hidden;
                    -webkit-user-select: none;
                    user-select: none;
                    width: 284px;

                    .media-list-item-thumb{
                        position: relative;
                        width: 100%;

                        .play-icon{
                            position: absolute;
                            right: 8px;
                            top: 8px;
                        }
                    }

                    .media-list-item-title{
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        color: black;
                        display: -webkit-box!important;
                        font-size: 1rem;
                        font-weight: 400;
                        margin: 16px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: normal;

                    }

                    :first-child{
                        //margin-left: 16px;
                    }
    
                    :last-child{
                        margin-right: 16px;
                    }

                }

            }
        }
    }
}


